import * as React from 'react';
import { store } from './store/store';
import { Provider } from 'react-redux';
import MainScreen from './Screens/MainScreen/MainScreen';


function App() {
  const tg = window.Telegram.WebApp;
  
  return (
      <Provider store={store}>
        <MainScreen tg = { tg }/>
      </Provider>
  );
}

export default App;
